<template>
    <div>
        <transition name="fade">
            <div class="menu menu--mod" v-if="memorial && showHamburger">
                <div class="menu__top">
                    <img class="menu__humburger" src="~@/assets/img/icon/humburger-close.svg"
                         @click.prevent="showHamburger = false"
                    >
                    <div class="menu__header" v-html="memorial.name">
                    </div>
                    <div class="menu__empty"></div>
                </div>

                <div class="menu__middle menu__middle--mod">
                    <router-link :to="{name: 'home'}" class="menu__link menu__link--mod active">
                        {{ $ml.get('memorial.header.toHome') }}
                    </router-link>
                    <router-link :to="{ name: 'memorials.life', params: { slug: this.memorial.slug } }"
                                 class="menu__link menu__link--mod">
                        {{ $ml.get('memorial.header.life') }}
                    </router-link>
                    <router-link :to="{ name: 'memorials.relatives', params: { slug: memorial.slug } }"
                                 class="menu__link menu__link--mod">
                        {{ $ml.get('memorial.header.relatives') }}
                    </router-link>
                </div>

                <div class="menu__bottom">
                    <div class="menu__title">{{ $ml.get('menu.contacts') }}</div>
                    <div class="menu__conection">
                        <div class="menu__conection-item">
                            <a class="menu__relink" :href="$ml.get('contacts.whatsapp.url')">
                                <img src="~@/assets/img/icon/menu-whatsap.svg" alt="">
                            </a>
                            <span>{{ $ml.get('contacts.whatsapp.text') }}</span>
                        </div>
                        <!--                        <div class="menu__conection-item">-->
                        <!--                            <a class="menu__relink" :href="$ml.get('contacts.camera.text')">-->
                        <!--                                <img src="~@/assets/img/icon/menu-camera.svg" alt="">-->
                        <!--                            </a>-->
