        computed: {
            ...mapGetters({
                memorial: 'story/memorial',
                periods: 'period/getPeriodsDefault',
                stories: 'story/getStories',
                isMemorialExist: 'story/isMemorialExist',
                isLoggedIn: 'auth/isLoggedIn',
                token: 'auth/token',
            }),
          cover() {
            let cover = this.covers.find(i => i.id == this.memorial.profilePhotoBackground);

            return cover ? cover.file : '';
          }
        },
        methods: {
          openHelp() {
            EventBus.$emit('help-center-open');
          },
          handleScroll() {
              this.stickyHeader = window.scrollY > 100;
          },
          getPeriodImage(period) {
            if(period.thumbnail) {
              return period.thumbnail.url;
            } else {
              if(period.defaultImageName) {
                return require(`@/assets/img/icon/periods/${period.defaultImageName}/1x.png`);
              } else {
                return require('@/assets/img/reserve-bg-cut.png');
              }
            }
          },
        },
        beforeMount() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        async mounted() {
            await this.$store.commit('story/setMemorial', null);
            await this.$store.commit('story/setMemorialExist', false);
            await this.$store.commit('period/setPeriods', []);
            await this.$store.commit('story/saveStories', []);

            if (!this.token) {
                await this.$store.dispatch('auth/getToken');
            }

            if (this.$store.getters['auth/isLoggedIn'](['customer'])) {
                await this.$store.dispatch('story/getOwnerMemorial', this.$route.params.slug);
            } else {
                await this.$store.dispatch('story/getMemorial', this.$route.params.slug);
            }

            if (!this.isMemorialExist) {
                return this.$router.push({name: 'home'});
            }

            if(this.memorial.lifeStoriesVisible) {
                await this.$store.dispatch('period/getPeriods', {id: this.memorial.id, withGeneral: false});
            }

            if(this.memorial.relativesStoriesVisible) {
                await this.$store.dispatch('story/getRandomStories', {id: this.memorial.id, count: 6});
            }

            var mySwiper = new Swiper('.swiper-container', this.swiperOptions)

            const full = document.querySelector('.full-card');
            full.style.height = window.innerHeight + 'px';
        }
    }
</script>

